<template>
  <div id="app">
    <rwheader></rwheader>
    <router-view></router-view>
    <rwfooter></rwfooter>
  </div>
</template>

<script>

import Header from '@/components/Header'
import Footer from '@/components/Footer'

export default {

  name: 'app',

  components: {

    'rwheader': Header,
    'rwfooter': Footer
  },

}
</script>

<style lang = 'scss'>

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 0;
}

.selected {
    color: white;
  }

  #benefits {

    color: white bold;
    background-color: #3c763d;
  }

  #logo {

    font-size: 25px;
    color: white;
  }

  #logoimg {

    margin-top: -2%;
  }

  .jumbotron {
    background-color: white !important
  }

  h1, h2 {

    font-weight: normal;
  }

  ul {

    list-style-type: none;
    padding: 0;
  }

  li {

    display: inline-block;
    margin: 0 10px;
  }

  a {

    color: #42b983;
  }
</style>



